import React from 'react'
import {Menu, Dropdown, Modal} from 'antd';
import {ReactComponent as Notifications} from "../../images/notifications.svg";
import ProfileIcon from '../ProfileIcon'
import {Row, Col} from 'antd'
import Avatar from "../../images/avatar.jpg";
import Avatar2 from "../../images/avatar2.jpg";
import { ExclamationCircleOutlined } from '@ant-design/icons'
import { useHistory } from 'react-router-dom';

export default function Notification(props) {
    const history = useHistory();
    const {me} = props
    async function UpdateMoney() {
        const dataMoney = {
            _id: me?._id,
            money: me?.money - 5000
        }
            const response = await fetch('https://qua-giang-app.herokuapp.com/user/update', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(dataMoney),
            })
    
            const res = await response.json()
    
            if (res) {
                history.push('/profile')
    
            } else {
                alert(res.error)
            }
        }
    const content = (
        <div>
            <div>im i: Ktx Pha Ty</div>
            <div>im n: Vit Hn IT</div>
            <div>Thi gian: </div>
            <div>Ngy: </div>
            <div>S tin phi tr: $5000</div>
            <div>T ng thanh ton sau 24h</div>
        </div>
    )
    function confirm() {
        Modal.confirm({
            title: 'Xc nhn thanh ton chuyn i',
            icon: <ExclamationCircleOutlined />,
            content: content,
            okText: 'Thanh ton ngay',
            cancelText: 'Hy',
            onOk: ()=>UpdateMoney()
        });
        }
    const menu = (
        <Menu>
            <Menu.Item key="0">
                <Row onClick={confirm} ><ProfileIcon iconSize='small' image={Avatar}/>ng  thanh ton cho chuyn i ca Phan V Mai Nhi</Row>
            </Menu.Item>
            <Menu.Divider/>
            <Menu.Item key="1">
                <Row><ProfileIcon iconSize='small'  image={Avatar2}/>Nguyn Vn Long  Book chuyn i ca bn</Row>
            </Menu.Item>
            <Menu.Divider/>
            <Menu.Item key="2">
                <Row><ProfileIcon iconSize='small' image={Avatar}/>Phan ng  to chuyn i mi</Row>
            </Menu.Item>
            <Menu.Divider/>
            <Menu.Item key="3">
                <Row><ProfileIcon iconSize='small' image={Avatar2}/>Bn c cc gi  theo di mi</Row>
            </Menu.Item>
            <Menu.Divider/>
            <Menu.Item key="4">
                <Row><ProfileIcon iconSize='small' image={Avatar}/>Li nhc: chuyn i ca bn bt u 10:20pm</Row>
            </Menu.Item>
        </Menu>
    );
    return (
            <Dropdown overlay={menu} trigger={['click']} placement="bottomCenter">
                <Notifications className='icon' onClick={e => e.preventDefault()}/>
            </Dropdown>

    )
}
